.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.sumInfo {
  display: contents;
  border: 1px groove black;
  border-radius: 7px;
}

.App-header {
  background-color: aliceblue;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.fingerP {
  display:  flow-root;
  background-color: white;
  max-width: 80%;
  width: auto;
  height: auto;
  word-break: break-all;
  border-style: groove;
  border-radius: 10px;
  padding: 5px;
  margin: 2px;
}

.App-link {
  color: #61dafb;
}

.fingers {
  display: flex;
}

.startBtn {
  margin: 5px;
}

.spanInfoText {
  background-color: aliceblue;
  display: block;
  margin: 5px;
  padding: 5px;
  border: groove black 1px;
  border-radius: 7px;
}

.spanInfoText:hover {
  cursor: pointer;
  background-color: #bcfcf9;
}

.arrowUp:after{
  padding: 5px;
  font-family: FontAwesome, serif;
  content:"\f106";
}

.arrowDown:after{
  padding: 5px;
  font-family: FontAwesome, serif;
  content:"\f107";
}

.infoText {
  background-color: beige;
  max-width: 1000px;
  margin: 2px;
  padding: 5px;
  border: groove black 1px;
  border-radius: 7px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
